<template>
  <div class="qrCode">
    <div class="new_site">
      <div class="btns">
        <Button type="primary" class="newBtn" @click="addQrCode">
          <Icon type="md-add" />
        </Button>
      </div>
    </div>

    <Table :columns="columns1" :data="data1" border>
      <template slot-scope="{ row, index }" slot="image">
        <div class="pic" ref="pic">
          <img :src="row.image" alt ref="pic" v-if="row.image"/>
           <div v-else style="color:red"></div>
        </div>
      </template>

      <template slot-scope="{ row, index }" slot="business">
        <div v-if="row.business.business_name">{{row.business.business_name}}</div>
        <div v-else style="color:red"></div>
      </template>

      <template slot-scope="{ row, index }" slot="action">
        <Button type="primary" size="small" style="margin-right: 5px" @click="downLoad(row)"></Button>
        <Button type="error" size="small" @click="linkMerchantClick(row.id)"></Button>
      </template>
    </Table>

    <!--  -->
    <Modal v-model="addQrCodeModal" title="">
      <Form :label-width="80">
        <FormItem label="" required>
          <InputNumber :max="10" :min="1" v-model="addQrCodeNum"></InputNumber>
        </FormItem>
      </Form>

      <div slot="footer">
        <Button type="text" size="large" @click="addQrCodeCancel"></Button>
        <Button type="primary" size="large" @click="addQrCodeClick"></Button>
      </div>
    </Modal>

    <!--  -->
    <Modal v-model="linkMerchantModal" :title="`:${linkMerchantTitle}`">
      <Form :label-width="80">
        <FormItem label="" required>
          <Select
            v-model="linkMerchant"
            style="width:120px"
            placeholder=""
            @on-change="selectLinkMerchant"
          >
            <Option v-for="item in linkMerchantList" :value="item.id" :key="item.id">{{ item.name }}</Option>
          </Select>
        </FormItem>
      </Form>

      <div slot="footer">
        <Button type="text" size="large" @click="linkMerchantCancel"></Button>
        <Button type="primary" size="large" @click="linkMerchantSure"></Button>
      </div>
    </Modal>
    <div class="page_box">
      <div class="page_left">
        {{ total }} {{ page }}/{{
        Math.ceil(total / pagesize)
        }}
      </div>
      <div class="page_right">
        <Page :total="total" :page-size="pagesize" :current="page" @on-change="changePage" />
      </div>
    </div>
  </div>
</template>

<script>
import qrCode from "./qrCode.js";
export default qrCode;
</script>
<style lang="less" scoped>
@import url("./qrCode.less");
</style>


